import Detail from './detail'
import None from './noNumber'
import Loading from './Loading'
import Map from './map'
import Head from './head.vue'
import Bus from '../assets/eventBus'
import loadingImg from './loadingImg'
  export default {
      name: "moreActive",
    data(){
        return{
          api:this.GLOBAL.baseURL,
          date: '',
          person:'',
          addClick:false,
          otherList:[
            {person:'成人+',adult:0},
          ],
          tiyan:'',
          imgUrl: '../../static/img/static/back.jpg',
          false:false,
          adress:'',
          closeIndex: '',
          price:[0,0],
          activeList:[],
          pickerOptions2: {
            disabledDate:(time)=> {
              return time.getTime() < Date.now() - 8.64e7;
            }
          },
          pageIndex:1,
          total:0,
          allprice:"",
          allperson:"",
          editSearch:'搜索',
          content:'',
          isLoading:false,
          cityData:''
        }
    },
    components:{
        Head,
      Map,
      loadingImg,
      Loading,
      None,
      Detail
    },
    watch:{
        content:function(){
          if(!this.content){
            this.getall()
          }
        },
      allperson:function(){
        if(this.allperson < 17){
          this.getall()
        }else{
          this.addClick = true
        }
      },
      date:function(){
          this.getall()
      },
      allprice:function(){
          this.getall()
      },
      tiyan:function(){
        this.getall()
      },
      closeIndex:function(){
          this.getall()
      }
    },
    methods:{
      chooseMoney(val){
        if(val == 1){
          this.allperson = this.person
        }else{
          this.allprice = this.price
        }
      },
        getall(){
          this.getActive(this.pageIndex,this.price[0],this.price[1],this.date?this.date[0]/1000:0,this.date?this.date[1]/1000:0,this.tiyan,this.closeIndex,this.allperson)
        },
      show(){
        this.$nextTick(() => {
          this.$refs.saveDateInput.focus()
        })
      },
      reduce(item,index){
        if(index == 0){
          if(item.adult > 0){
            this.addClick = false
            item.adult = item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }
        }else{
          if(item.adult !=0){
            this.addClick = false
            item.adult=item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }else{
            item.adult = 0
          }
        }
      },
      add(item,index){
        if(this.person < 16){
          this.addClick = false
          item.adult = item.adult + 1
          let b =0
          for(let i = 0;i<this.otherList.length;i++){
            b = b + this.otherList[i].adult
          }
          this.person = b
        }else{
          this.addClick = true
        }

      },
      rejest(){
        this.price = [0,0]
      },
      change(){
        if(this.closeIndex){
          this.closeIndex = ''
        }else{
          this.closeIndex = 1
        }
      },
      getActive(val,price_low,price_high,begin_time,end_time,kind_id,is_volunteen,person){
        this.isLoading = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          city: this.adress,
          keywords: this.content,
          page:val,
          price_low:price_low,
          price_high:price_high,
          activ_begin_time:	begin_time,
          activ_end_time:end_time,
          kind_id:kind_id,
          is_volunteen:is_volunteen,
          max_person_num: person
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.activeList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
               .then(res=>{
                 localStorage.setItem('token',res.data.data)

               })
            }else{
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
        this.pageIndex = val
        this.getall()
      },
      getVal(msg){
        this.content = msg
      },
      searchHome(){
        this.getall()
      },
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id
          }
        })
      }
    },
    mounted(){
        window.scroll(0,0)
        this.getActive(1)
    },
    created(){
        this.adress = this.$route.query.key
      document.title = this.$route.query.key
      this.cityData = JSON.parse(this.$route.query.information)
    }
  }



